.about {
  position: relative;
  height: calc(100vh - 80px);
  background-color: $darkShades;
  text-align: center;
  color: $light-color;

  &__divider {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);

    svg {
      position: relative;
      display: block;
      width: calc(100% + 1.3px);
      transform: rotateY(180deg);
      height: 50px;
      @include mq("md") {
        height: 100px;
      }
    }

    &--fill {
      fill: $dark1;
    }
  }
}
